@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=JetBrains+Mono:ital,wght@1,200&display=swap);
.bt {
    text-align: center; 
}

.bt input {
    font-family: 'Rubik', sans-serif;
    border-radius: 30px;
    font-size: 25px;
    width: 350px;
    border-width: 0;
    outline: 0;
    padding: 7px;
    box-shadow: 3px 5px 5px rgba(0, 0, 0, 0.24);
    margin: 20px;
}

.bt .calc {
    width: 250px;
    cursor: pointer;
}

.imc {
    position: relative;
    top: 75px;
    background-color: rgb(16, 43, 100);
    width: 700px;
    height: 200px;
    margin: auto;
    border-radius: 30px;
    box-shadow: 3px 5px 5px rgba(0, 0, 0, 0.24);
}

@media (max-width: 900px) {
    .bt input {
        width: 80%;
    }

    .bt .calc {
        width: 40%;
    }

    .imc {
        width: 70%;
    }
}
* {
    margin: 0;
    padding: 0;
}

body {
    background-color: rgb(4, 27, 77);
    font-family: 'JetBrains Mono', monospace;
}

.App h1 {
    padding: 40px;
    color: white;
    text-align: center;
    font-size: 50px;
}

@media (max-width: 900px) {
    .App h1 {
       width: 70%;
       font-size: 30px;
       margin: auto; 
    }
}
